.root{
  display: flex;
  width: 100%;
  flex-direction: column;
  position: relative;

  .label{
    padding-bottom: 5px;
  }
  
  .wrapper{
    display: flex;
    flex-direction: row;
    z-index: 1;

    .read-only-value{
      font-weight: unset;
    }
    
    .text-field{
      flex: 1;
      display: flex;


      .input{
        width: 100%;
        padding: 10px;
        input{
          padding: 0 !important;
        }
      }

      .date-button{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 40px;

        button{
          padding: 8px;
        }
        .label{
          padding: 0;
        }
      }
     
    }
  }

  .date{
    position: absolute;
    right: 0;
    bottom: 0;
    padding-right: 10px;
  }
  
}