@import 'styles/color.scss';


.root{
  height: 450px;
  border-radius: 10px !important;

  
  .wrapper{
    padding: 20px;
    height: 100%;
    display: flex;
    flex-direction: column;
    
    .header{
      display: flex;
      flex-direction: column;
      
      .input{
        padding-left: 10px;
      }

      .first-row{
        display: flex;
        flex: 1;

        .count{
          h5{
            font-weight: bold
          }
        }
    
        .filter{
          flex: 1;
          display: flex;
          justify-content: flex-end;
        }
      }

      .second-row{
        display: flex;
        flex: 1;
        align-items: center;
        margin: 10px 0;
        
        .title{
          color: $color-12;
          font-weight: bold;
          flex: 1;
        }
        

    
        .filter{
          flex: 1;
          display: flex;
          justify-content: flex-end;
        }
      }
    
    }
  
   
  
    .list{
      flex: 1;
      overflow: auto;
      display: flex;
      flex-direction: column;
    }

    .action{
      display: flex;
      justify-content: flex-end;
      font-weight: bold;
      color: $color-12;
    }
  }  
}