.root{
  display: flex;
  width: 100%;
  flex-direction: column;

  .label{
    padding-bottom: 5px;
  }
  
  .wrapper{
    display: flex;
    flex-direction: row;

    .read-only-value{
      font-weight: unset;
    }
    
    .select-container{
      flex: 1;
      display: flex;


      .select{
        padding: 0;
      }
      
      .input{
        width: 100%;
        padding: 10px;
        input{
          padding: 0 !important;
          text-align: right;
        }
      }
    }
  }
  
}