.root{
  display: flex;
  width: 100%;
  flex-direction: column;
  position: relative;

  .label{
    padding-bottom: 5px;
  }
  
  .wrapper{
    display: flex;
    flex-direction: row;

    .read-only-value{
      font-weight: unset;
    }

    .autocomplete-field{
      flex: 1;
      display: flex;

      .form-control{
        width: 100%;
      }

      .input{
        width: 100%;
        padding: 10px;
        input{
          padding: 0 !important;
        }
      }

      .suggestions{
        z-index: 1;
        position: absolute;
        margin-top: 40px;
        width: 100%;
      }
    }
  }
  
}