.root{
  display: flex;
  width: 100%;
  flex-direction: column;

  .label{
    padding-bottom: 5px;
  }
  
  .wrapper{
    display: flex;
    flex-direction: row;
    
    .read-only-value{
      font-weight: unset;
    }
    
    .text-field{
      flex: 1;
      display: flex;


      .input{
        width: 100%;
        padding: 10px;
        input{
          padding: 0 !important;
          text-align: right;
        }
      }

      .currency{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 40px;
        border-radius: 0 4px 4px 0;
        background-color: #c0c0c0;

        .label{
          padding: 0;
        }
      }
     
    }
  }
  
}