.root{
  display: flex;
  width: 100%;
  flex-direction: column;

  .label{
    padding-bottom: 5px;
  }
  .wrapper{
    display: flex;
    flex-direction: row;

    .icon-container{
      display: flex;
      padding-left: 5px;
      align-items: center;
      font-size: 15px;
      color: red;
      width: 30px;

      .icon{
        margin: 5px;
        cursor: pointer;
        background: #facd91;
        border-radius: 3px;
        padding: 2px;
        
      }

      
    }

    
 
  }
  
}

.message{
  padding: 10px !important;
}