.root{
  display: flex;
  flex-direction: column;

  .header{
    display: flex;
    position: relative;
    padding-top: 20px;

    .anual-benefit{
      min-width: 200px;
      position: absolute;
    }

    .line-graph{
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1;

      .container{
        min-width: 600px;

        .line-container{
          widows: 100%;
          display: flex;

          .item{
            
            min-height: 5px;
          }
        }

        .line-details{
          display: flex;
          margin-top: 10px;

          .item{
            display: flex;
            flex: 1;
            flex-direction: column;

            .label{
              display: flex;

              .color{
                height: 15px;
                width: 15px;
                margin-right: 10px;
              }
            }
          }
        }
      }
    }
  }

  td{
    vertical-align: unset;
  }

  .cell{
    display: flex;
    flex-direction: column;

    .label{
      font-weight: bold;
      margin-top: 10px;
    }
  }
  .total{
    display: flex;
    justify-content: flex-end;
  }

  .icon{
    color: #29a11e;
    cursor: pointer;
  }
}

