.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    display: flex;
    flex-direction: row;

    .read-only-value {
      font-weight: unset;
    }

    .text-field {
      flex: 1;
      // padding: 10px; removed for divider component
      display: flex;
      .form-control {
        width: 100%;
      }
      &.dicrepancy {
        background-color: #facd91;
        .divider {
          background: #d1a364;
        }
        .end-adornment {
          color: #b68d53;
        }
      }

      .input {
        width: 100%;
        padding: 10px;
        input {
          padding: 0 !important;
        }
      }
      .input-amount {
        input {
          padding: 0 !important;
          text-align: right;
        }
      }
      .end-adornment {
        color: #8b8b8b;
        font-weight: 500;
        padding: 10px;
      }
      .select {
        color: #000000;
        padding: 4px;
        padding-left: 10px;
      }
    }
  }
}
