.root{
  display: flex;
  width: 100%;
  flex-direction: column;

  .header{
    display: flex;
    padding-bottom: 5px;

    .description{
      flex: 1;
    }

    .code{
      width: 90px;
      margin: 0 10px;
    }

    .primary{
      width: 60px;
      text-align: center;
    }
  }

  
  .wrapper{
    padding-bottom: 10px;

    &:last-child{
      padding-bottom: unset;
    }
    
    .sub-wrapper{
      display: flex;
      
  
      .description{
        flex: 1
      }
  
      .code{
        width: 90px;
        margin: 0 10px;
      }
  
      .primary{
        width: 60px;
        text-align: center;
      }
  
      .text-field{
        padding: 10px;
        display: flex;
  
        &.dicrepancy{
          background-color: #facd91;
        }
        .input{
          width: 100%;
  
          input{
            padding: 0 !important;
          }
        }
       
      }
  
      .icon-container{
        display: flex;
        padding-left: 5px;
        align-items: center;
        font-size: 15px;
        color: red;
        width: 30px;
  
        .icon{
          margin: 5px;
          cursor: pointer;
        } 
      }   
    }

    .description-link{
      padding-top: 5px;
      font-weight: 500;
      text-decoration: underline;
      cursor: pointer;
    }
  }
  
  
}

.message{
  padding: 10px !important;
}