@import 'styles/color.scss';

.header{
  position: relative !important;

  .title{
    margin-left: 10px;
    flex: 1
  }
}

.root{
  display: flex;
  flex-direction: column;
  
  .list-header{
    margin-top: 20px;
    color: $color-12;
  }

  .actions{
    padding: 40px 0 10px 0 !important;
    display: flex;
    justify-content: flex-end;
  }

  .bundle-details{
    padding: 10px;
    
    .batch-list{
      .header{
        margin: 30px 0;
        flex-direction: row;
        align-items: center;
      }
    }

  }

  .table-container {
    padding: 10px;
    margin-top: 10px;
  }
  
}
.buttonWrapper{
  margin-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
