@import 'styles/color.scss';

.root{

  .sub-container{
    padding: 20px;
  }

  .dialog-container{
    width: 900px;
    height: 1000px;
  }

  .text-fields{
    margin-bottom: 30px;
    .input-container{
      padding: 0 12px 0 12px !important;
    }
    .ellipsis-container{
      position: relative;
      margin-top: 30px;
      left: 98%;
      right: 20px;
      width: 50px;
      height: 40px;
    }
  }
 
  .icd-10s{
    margin-bottom: 20px;
  }

  .lower-container{
    display: flex;

    .row{
      &.discrepancy{
        background-color: #facd91;
      }
    }

    .error-column{
      background-color: white;
      width: 20px;
    }

    .amt-column {
      text-align: right;
    }

    .out-patient{
      display: flex;
      flex-direction: column;
      padding: 20px;
      width: 600px;
      height: 100%;
      
      .table{
        margin-bottom: 20px;
        flex: 1;
      }
      

      .table-header{
        display: flex;
        padding-bottom: 10px;
        .table-description{
          flex: 1;
          color: $color-1;
        }

        .add-link{
          font-weight: 500;
          text-decoration: underline;
          cursor: pointer;
          color: #0B4930;
        }

        .add-link:hover{
          color: #1CC07E;
        }
      }


      .table-total{
        display: flex;
        padding-top: 10px;
        width: 100%;
        .caption{
          flex: 1;
          display: flex;
          justify-content: flex-end;
          padding-right: 20px;

          font-weight: bold;
        }

        .amount{
          font-weight: bold;
        }
      }
      
    }
    
    .tabs{
      flex: 1;
      padding-left: 20px;
    }
  }
}
.input{
  height:50px;
  width: 100px;}

.benefits-table{
  .header{
    font-weight: bold;
    color: #000000
  }
}
.table-grid{
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 10px;
}

.buttonWrapper{
  margin-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}