.root {
  margin-left: 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  .neutral {
    background-color: #2bb6cf;
  }

  .neutral:hover {
    background-color: #166573;
  }

  .positive {
    background-color: #1cc07e;
  }

  .positive:hover {
    background-color: #0b4930;
  }
}
